





























import { Component, Prop, Vue, Watch } from "vue-property-decorator";

@Component({})
export default class SizeBeadsBox extends Vue {
  @Prop() sizeBase!: string;
  @Prop() customStyleOfString!: string;
  @Prop() show!: boolean;
  active_el: number = 1;
  activate(el: number) {
    this.active_el = el;
  }
  defautlData: Array<any> = [
    { classOfBead: "classic_medium10_women", name: "10mm" },
    { classOfBead: "classic_medium8_women", name: "8mm" },
    { classOfBead: "classic_medium6_women", name: "6mm" },
  ];
  @Watch("sizeBase")
  wSizeBase() {
    if (
      this.sizeBase == "classic_medium8_women" ||
      this.sizeBase == "classic_medium10_women" ||
      this.sizeBase == "classic_medium6_women"
    ) {
      this.defautlData = [
        { classOfBead: "classic_medium10_women", name: "10mm" },
        { classOfBead: "classic_medium8_women", name: "8mm" },
        { classOfBead: "classic_medium6_women", name: "6mm" },
      ];
    }
    if (
      this.sizeBase == "classic_large8_women" ||
      this.sizeBase == "classic_large10_women" ||
      this.sizeBase == "classic_large6_women"
    ) {
      this.defautlData = [
        { classOfBead: "classic_large10_women", name: "10mm" },
        { classOfBead: "classic_large8_women", name: "8mm" },
        { classOfBead: "classic_large6_women", name: "6mm" },
      ];
    }
    if (
      this.sizeBase == "classic_small8_women" ||
      this.sizeBase == "classic_small10_women" ||
      this.sizeBase == "classic_small6_women"
    ) {
      this.defautlData = [
        { classOfBead: "classic_small10_women", name: "10mm" },
        { classOfBead: "classic_small8_women", name: "8mm" },
        { classOfBead: "classic_small6_women", name: "6mm" },
      ];
    }
    if (
      this.sizeBase == "classic_xsmall8_women" ||
      this.sizeBase == "classic_xsmall10_women" ||
      this.sizeBase == "classic_xsmall6_women"
    ) {
      this.defautlData = [
        { classOfBead: "classic_xsmall10_women", name: "10mm" },
        { classOfBead: "classic_xsmall8_women", name: "8mm" },
        { classOfBead: "classic_xsmall6_women", name: "6mm" },
      ];
    }

    if (
      this.sizeBase == "elastic_medium8_women" ||
      this.sizeBase == "elastic_medium10_women" ||
      this.sizeBase == "elastic_medium6_women"
    ) {
      this.defautlData = [
        { classOfBead: "elastic_medium10_women", name: "10mm" },
        { classOfBead: "elastic_medium8_women", name: "8mm" },
        { classOfBead: "elastic_medium6_women", name: "6mm" },
      ];
    }
    if (
      this.sizeBase == "elastic_large8_women" ||
      this.sizeBase == "elastic_large10_women" ||
      this.sizeBase == "elastic_large6_women"
    ) {
      this.defautlData = [
        { classOfBead: "elastic_large10_women", name: "10mm" },
        { classOfBead: "elastic_large8_women", name: "8mm" },
        { classOfBead: "elastic_large6_women", name: "6mm" },
      ];
    }
    if (
      this.sizeBase == "elastic_small8_women" ||
      this.sizeBase == "elastic_small10_women" ||
      this.sizeBase == "elastic_small6_women"
    ) {
      this.defautlData = [
        { classOfBead: "elastic_small10_women", name: "10mm" },
        { classOfBead: "elastic_small8_women", name: "8mm" },
        { classOfBead: "elastic_small6_women", name: "6mm" },
      ];
    }
    if (
      this.sizeBase == "elastic_xsmall8_women" ||
      this.sizeBase == "elastic_xsmall10_women" ||
      this.sizeBase == "elastic_xsmall6_women"
    ) {
      this.defautlData = [
        { classOfBead: "elastic_xsmall10_women", name: "10mm" },
        { classOfBead: "elastic_xsmall8_women", name: "8mm" },
        { classOfBead: "elastic_xsmall6_women", name: "6mm" },
      ];
    }
  }
  // @Watch("customStyleOfString")
  // restartActive() {
  //   this.active_el = 2;
  // }
  @Watch("customStyleOfString")
  wResetDefaultActive() {
    this.active_el = 2;
  }
  changeSizeBead(size: any, name: any) {
    let val = [size, name];

    this.$emit("changeSizeBead", val);
  }

  closer() {
    this.$emit("closeStyleOfString");
  }
}
