
























import { Component, Watch, Prop, Vue } from "vue-property-decorator";
import MetaTrail from "@/components/LeftWingModule/MetaTrail.vue";
import PotHolder from "@/components/LeftWingModule/PotHolder.vue";
import { BillBead } from "@/store/Object";
@Component({
  components: {
    MetaTrail,
    PotHolder,
  },
})
export default class LeftWing extends Vue {
  @Prop() colorOfFrontString?: string;
  @Prop() colorOfBackString?: string;
  @Prop() colorOfLock?: string;
  @Prop() sizeBase!: string;
  @Prop() customColorName!: string;
  @Prop() customLockName!: string;
  @Prop() customStyleOfString!: string;
  @Prop() sizeBead!: string;
  numberBead: number = 12;

  numberBeadOfString(val: number) {
    this.numberBead = val;
    console.log(this.numberBead);
  }
  price: number = 200;

  beadSlot: Array<BillBead> = [];
  listPrice: Array<any> = [];
  addBead(data: any) {
    if (this.beadSlot.length != 0) {
      this.beadSlot.push(data);
      for (let i = 0; i < this.beadSlot.length - 1; i++) {
        if (this.beadSlot[i].slot == data.slot) {
          this.beadSlot[i].name = data.name;
          this.beadSlot[i].price = data.price;

          this.beadSlot[i].id = data.id;
        }
      }
      this.beadSlot = this.beadSlot.filter(
        (thing, index, self) =>
          index === self.findIndex((t) => t.slot === thing.slot)
      );
      this.beadSlot.sort(function(a: BillBead, b: BillBead): any {
        if (a.slot < b.slot) return -1;
      });
    } else {
      this.beadSlot.push(data);
    }
    this.listPrice = this.beadSlot.filter((b) => {
      return b.price;
    });
  }
  @Watch("sizeBase")
  wSizeBase() {
    this.listPrice = [];
    this.beadSlot = [];
  }
}
