
























































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import { Bead } from "@/store/Object";
import { Drag } from "vue-easy-dnd";
@Component({
  components: {
    Drag,
  },
})
export default class BeadBock extends Vue {
  @Prop() show?: boolean;
  @Prop() customStyleOfString!: string;

  preciousStoneBeads: Array<Bead> = [
    {
      id: 1,
      img: "./assets/PSBEAD01.png",
      name: "THẠCH ANH HỒNG",
      descripton: "TÌNH YÊU, HÒA HỢP, TÍCH CỰC",
      price: 0,
    },
    {
      id: 2,
      img: "./assets/PSBEAD02.png",
      name: "MÃ NÃO",
      descripton: "TRÍ TUỆ, TỰ TIN, THỊNH VƯỢNG",
      price: 30,
    },
  ];
  preciousMetalBeads: Array<Bead> = [
    {
      id: 3,
      img: "./assets/PMBEAD01.png",
      name: "BẠC",
      descripton: "GIÀU CÓ, THỊNH VƯỢNG, VỮNG CHÃI",
      price: 0,
    },
    {
      id: 4,
      img: "./assets/PMBEAD02.png",
      name: "HOA HỒNG VÀNG",
      descripton: "SỨC KHỎE, ỔN ĐỊNH, TỰ TIN",
      price: 30,
    },
  ];

  @Watch("customStyleOfString")
  wCustomStyleOfString() {
    if (this.customStyleOfString == "Classic With String") {
      this.preciousStoneBeads = [
        {
          id: 1,
          img: "./assets/PSBEAD01.png",
          name: "THẠCH ANH HỒNG",
          descripton: "TÌNH YÊU, HÒA HỢP, TÍCH CỰC",
          price: 0,
        },
        {
          id: 2,
          img: "./assets/PSBEAD02.png",
          name: "MÃ NÃO",
          descripton: "TRÍ TUỆ, TỰ TIN, THỊNH VƯỢNG",
          price: 30,
        },
      ];
      this.preciousMetalBeads = [
        {
          id: 3,
          img: "./assets/PMBEAD01.png",
          name: "BẠC",
          descripton: "GIÀU CÓ, THỊNH VƯỢNG, VỮNG CHÃI",
          price: 0,
        },
        {
          id: 4,
          img: "./assets/PMBEAD02.png",
          name: "HOA HỒNG VÀNG",
          descripton: "SỨC KHỎE, ỔN ĐỊNH, TỰ TIN",
          price: 30,
        },
      ];
    }
    if (
      this.customStyleOfString == "Classic" ||
      this.customStyleOfString == "Elastic"
    ) {
      this.preciousStoneBeads = [
        {
          id: 1,
          img: "./assets/newPSBEAD01.png",
          name: "THẠCH ANH HỒNG",
          descripton: "TÌNH YÊU, HÒA HỢP, TÍCH CỰC",
          price: 0,
        },
        {
          id: 2,
          img: "./assets/newPSBEAD02.png",
          name: "MÃ NÃO",
          descripton: "TRÍ TUỆ, TỰ TIN, THỊNH VƯỢNG",
          price: 30,
        },
      ];
      this.preciousMetalBeads = [
        {
          id: 3,
          img: "./assets/newPMBEAD05.png",
          name: "BẠC HOLLOW",
          descripton: "GIÀU CÓ, THỊNH VƯỢNG, VỮNG CHÃI",
          price: 0,
        },
        {
          id: 4,
          img: "./assets/newPMBEAD06.png",
          name: "BẠC CARO CỔ ĐIỂN",
          descripton: "GIÀU CÓ, THỊNH VƯỢNG, VỮNG CHÃI",
          price: 30,
        },
      ];
    }
  }
  closer() {
    this.$emit("closeBead");
  }
}
