










































import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class StylesBox extends Vue {
  @Prop() show?: boolean;
  active_el: number = 2;
  activate(el: number) {
    this.active_el = el;
  }
  closer() {
    this.$emit("closeStyle");
  }

  switchStyle(value: string) {
    this.$emit("changeStyle", value);
  }
}
