






































import { Component, Watch, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class LockStyle extends Vue {
  @Prop() customStyleOfString!: string;
  @Prop() show!: boolean;
  @Prop() sizeBase!: string;
  active_el: number = 2;
  activate(el: number) {
    this.active_el = el;
  }
  @Watch("customStyleOfString")
  restartActive() {
    this.active_el = 2;
  }
  closer() {
    this.$emit("closeLockStyle");
  }
  changeLock(sizeBase: string, styleOfLock: string) {
    let url: string = "";
    if (sizeBase == "strung_large_women") {
      if (styleOfLock == "Mạ vàng 18 kara") {
        url = "./assets/MediumGold.png";
      }
      if (styleOfLock == "Mạ bạc") {
        url = "./assets/MediumSilver.png";
      }
    } else if (sizeBase == "strung_medium_women") {
      if (styleOfLock == "Mạ vàng 18 kara") {
        url = "./assets/SmallGold.png";
      }
      if (styleOfLock == "Mạ bạc") {
        url = "./assets/SmallSilver.png";
      }
      //
    } else if (sizeBase == "strung_small_women") {
      if (styleOfLock == "Mạ vàng 18 kara") {
        url = "./assets/XSmallGold.png";
      }
      if (styleOfLock == "Mạ bạc") {
        url = "./assets/XSmallSilver.png";
      }
    } else if (sizeBase == "strung_xlarge_women") {
      if (styleOfLock == "Mạ vàng 18 kara") {
        url = "./assets/LargeGold.png";
      }
      if (styleOfLock == "Mạ bạc") {
        url = "./assets/LargeSilver.png";
      }
    }

    if (
      sizeBase == "classic_medium8_women" ||
      sizeBase == "classic_small8_women" ||
      sizeBase == "classic_xsmall8_women" ||
      sizeBase == "classic_large8_women" ||
      sizeBase == "classic_medium6_women" ||
      sizeBase == "classic_small6_women" ||
      sizeBase == "classic_xsmall6_women" ||
      sizeBase == "classic_large6_women" ||
      sizeBase == "classic_medium10_women" ||
      sizeBase == "classic_small10_women" ||
      sizeBase == "classic_xsmall10_women" ||
      sizeBase == "classic_large10_women"
    ) {
      //
      if (styleOfLock == "Mạ vàng 18 kara") {
        url = "./assets/classic_gold.png";
      }
      if (styleOfLock == "Mạ bạc") {
        url = "./assets/classic_silver.png";
      }
    }

    if (
      sizeBase == "elastic_medium8_women" ||
      sizeBase == "elastic_small8_women" ||
      sizeBase == "elastic_xsmall8_women" ||
      sizeBase == "elastic_large8_women" ||
      sizeBase == "elastic_medium6_women" ||
      sizeBase == "elastic_small6_women" ||
      sizeBase == "elastic_xsmall6_women" ||
      sizeBase == "elastic_large6_women" ||
      sizeBase == "elastic_medium10_women" ||
      sizeBase == "elastic_small10_women" ||
      sizeBase == "elastic_xsmall10_women" ||
      sizeBase == "elastic_large10_women"
    ) {
      //
      if (styleOfLock == "Mạ vàng 18 kara") {
        url = "./assets/elastic_gold.png";
      }
      if (styleOfLock == "Mạ bạc") {
        url = "./assets/elastic_silver.png";
      }
    }

    let value = [url, styleOfLock];
    this.$emit("changeLockStlye", value);
  }
}
