































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import { listOfSizeString } from "@/store/Object";
@Component({})
export default class SizeBox extends Vue {
  @Prop() show?: boolean;
  @Prop() customStyleOfString!: string;
  active_el: number = 2;
  activate(el: number) {
    this.active_el = el;
  }
  @Watch("customStyleOfString")
  restartActive() {
    this.active_el = 2;
  }
  defaultSize: Array<listOfSizeString> = [
    {
      class: "strung_xlarge_women",
      nameSize: "Lớn nhất (18cm / 7.1”)",
    },
    {
      class: "strung_large_women",
      nameSize: "Lớn (17cm / 6.7”)",
    },
    {
      class: "strung_medium_women",
      nameSize: "Vừa (16cm / 6.3”)",
    },
    {
      class: "strung_small_women",
      nameSize: "Nhỏ (15cm / 5.9”)",
    },
  ];
  @Watch("customStyleOfString")
  wCustomStyleOfString() {
    if (this.customStyleOfString == "Classic With String") {
      this.defaultSize = [
        {
          class: "strung_xlarge_women",
          nameSize: "Lớn nhất (18cm / 7.1”)",
        },
        {
          class: "strung_large_women",
          nameSize: "Lớn (17cm / 6.7”)",
        },
        {
          class: "strung_medium_women",
          nameSize: "Vừa (16cm / 6.3”)",
        },
        {
          class: "strung_small_women",
          nameSize: "Nhỏ (15cm / 5.9”)",
        },
      ];
    }
    if (this.customStyleOfString == "Classic") {
      this.defaultSize = [
        {
          class: "classic_xsmall8_women",
          nameSize: "Nhỏ nhất (14cm / 5.5”)",
        },
        {
          class: "classic_small8_women",
          nameSize: "Nhỏ (15cm / 5.9”)",
        },
        {
          class: "classic_medium8_women",
          nameSize: "Vừa (16cm / 6.3”)",
        },
        {
          class: "classic_large8_women",
          nameSize: "Lớn (17cm / 6.7”)",
        },
      ];
    }
    if (this.customStyleOfString == "Elastic") {
      this.defaultSize = [
        {
          class: "elastic_xsmall8_women",
          nameSize: "Nhỏ nhất (14cm / 5.5”)",
        },
        {
          class: "elastic_small8_women",
          nameSize: "Nhỏ (15cm / 5.9”)",
        },
        {
          class: "elastic_medium8_women",
          nameSize: "Vừa (16cm / 6.3”)",
        },
        {
          class: "elastic_large8_women",
          nameSize: "Lớn (17cm / 6.7”)",
        },
      ];
    }
  }
  closer() {
    this.$emit("closeSize");
  }
  sizeThis(value: string) {
    this.$emit("sizeOfString", value);
  }
}
