







































import { Component, Prop, Vue, Watch } from "vue-property-decorator";

@Component({})
export default class StringColor extends Vue {
  @Prop() show?: boolean;
  @Prop() sizeBase!: string;

  active_el: number = 2;
  activate(el: number) {
    this.active_el = el;
  }
  @Watch("customStyleOfString")
  restartActive() {
    this.active_el = 2;
  }
  closer() {
    this.$emit("closeColorOfString");
  }
  changeColor(sizeBase: string, color: string) {
    let front: string = "";
    let back: string = "";
    if (sizeBase == "strung_large_women") {
      if (color == "white") {
        front = "./assets/StrungMediumWhite.png";
        back = "./assets/White_M.png";
      }
      if (color == "black") {
        front = "./assets/StrungMediumBlack.png";
        back = "./assets/Black_M.png";
      }
    } else if (sizeBase == "strung_medium_women") {
      if (color == "white") {
        front = "./assets/StrungSmallWhite.png";
        back = "./assets/White_S.png";
      }
      if (color == "black") {
        front = "./assets/StrungSmallBlack.png";
        back = "./assets/Black_S.png";
      }
      //
    } else if (sizeBase == "strung_small_women") {
      //
      if (color == "white") {
        front = "./assets/StrungXSmallWhite.png";
        back = "./assets/White_XS.png";
      }
      if (color == "black") {
        front = "./assets/StrungXSmallBlack.png";
        back = "./assets/Black_XS.png";
      }
    } else if (sizeBase == "strung_xlarge_women") {
      //
      if (color == "white") {
        front = "./assets/StrungLargeWhite.png";
        back = "./assets/White_L.png";
      }
      if (color == "black") {
        front = "./assets/StrungLargeBlack.png";
        back = "./assets/Black_L.png";
      }
    }

    if (
      sizeBase == "classic_medium8_women" ||
      sizeBase == "classic_small8_women" ||
      sizeBase == "classic_xsmall8_women" ||
      sizeBase == "classic_large8_women" ||
      sizeBase == "classic_medium6_women" ||
      sizeBase == "classic_small6_women" ||
      sizeBase == "classic_xsmall6_women" ||
      sizeBase == "classic_large6_women" ||
      sizeBase == "classic_medium10_women" ||
      sizeBase == "classic_small10_women" ||
      sizeBase == "classic_xsmall10_women" ||
      sizeBase == "classic_large10_women"
    ) {
      //
      if (color == "white") {
        front = "./assets/ClassicMediumWhite.png";
        back = "";
      }
      if (color == "black") {
        front = "./assets/ClassicMediumBlack.png";
        back = "";
      }
    }

    var value = [front, back, color];
    // console.log(front);

    this.$emit("changeColorOfString", value);
  }
}
