  numberOfBeadWhenChangeStyle(num: number) {
    for (let i = 0; i < 31; i++) {
      this.slot[i].display = false;
    }
    for (let i = 0; i < num; i++) {
      this.slot[i].display = true;
    }
    this.$emit("numberBeadOfString", num);
  }

  @Watch("sizeBase")
  wSizeBase() {
    this.loading = true;
    setTimeout(() => {
      this.loading = false;
    }, 2000);
    this.slot.map((a) => {
      a.dropped = false;
      a.data = "";
    });
    if (this.sizeBase == "strung_small_women") {
      this.numberOfBeadWhenChangeStyle(11);
    }
    if (this.sizeBase == "strung_medium_women") {
      this.numberOfBeadWhenChangeStyle(12);
    }
    if (this.sizeBase == "strung_large_women") {
      this.numberOfBeadWhenChangeStyle(13);
    }
    if (
      this.sizeBase == "strung_xlarge_women" ||
      this.sizeBase == "classic_xsmall10_women"
    ) {
      this.numberOfBeadWhenChangeStyle(14);
    }
    if (this.sizeBase == "classic_small10_women") {
      this.numberOfBeadWhenChangeStyle(15);
    }
    if (
      this.sizeBase == "classic_medium10_women" ||
      this.sizeBase == "elastic_xsmall10_women"
    ) {
