













































































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import { BillBead } from "@/store/Object";
@Component
export default class MetaTrail extends Vue {
  @Prop() beadSlot!: Array<BillBead>;
  @Prop() listPrice!: any;
  @Prop() sizeBead!: any;
  @Prop() sizeBase!: string;
  @Prop() customColorName!: string;
  @Prop() customLockName!: string;
  @Prop() customStyleOfString!: string;
  @Prop() numberBead!: number;

  enableCart: boolean = false;

  sizeName: any = "Vừa";
  stringName: string = "Chuỗi cổ điển";
  quantity: any = 1;
  basePrice: number = 200;
  totalPrice: number = 200;
  showInfo: boolean = false;

  checkStringInString(val: string) {
    let x = this.sizeBase.indexOf(val);

    if (x > 0) return true;
    else return false;
  }
  @Watch("customStyleOfString")
  wChangeStringName() {
    if (this.customStyleOfString == "Classic With String")
      this.stringName = "Chuỗi cổ điển";
    if (this.customStyleOfString == "Classic") this.stringName = "Cổ điển";
    if (this.customStyleOfString == "Elastic") this.stringName = "Co dãn";
  }
  @Watch("sizeBase")
  wChangeSizeName() {
    // console.log(this.sizeBase);
    if (this.checkStringInString("medium")) {
      this.sizeName = "Vừa";
    }
    if (this.checkStringInString("large")) {
      this.sizeName = "Lớn";
    }
    if (this.checkStringInString("xlarge")) {
      this.sizeName = "Lớn nhất";
    }

    if (this.checkStringInString("small")) {
      this.sizeName = "Nhỏ";
    }
    if (this.checkStringInString("xsmall")) {
      this.sizeName = "Nhỏ nhất";
    }
  }
  showFinal() {
    var one = this.beadSlot.map((b) => {
      return { Hạt: b.name, giá: b.price, "vị trí": b.slot };
    });

    var FinalDta = JSON.stringify(one);
    var quantity = JSON.stringify(parseInt(this.quantity));
    var totalPrice = JSON.stringify(this.totalPrice * parseInt(this.quantity));
    alert(
      "Vị trí vòng và bi:\n" +
        FinalDta +
        "\n số lượng: " +
        quantity +
        "\n giá tiền: " +
        totalPrice +
        "\n loại dây: " +
        this.customStyleOfString +
        "\n màu dây: " +
        this.customColorName +
        "\n loại khóa: " +
        this.customLockName
    );
  }
  @Watch("beadSlot", { deep: true })
  showInfoTable() {
    if (this.beadSlot.length != 0) {
      this.showInfo = true;
    } else {
      this.showInfo = false;
    }

    
    if (this.numberBead == this.beadSlot.length) {
      this.enableCart = true;
    }
    else this.enableCart = false
  }

  @Watch("listPrice")
  wListPrice() {
    let x = this.listPrice.map((p: any) => {
      x = p.price;
      return x;
    });
    let sum = 0;
    x.map((x: any) => (sum += x));
    this.totalPrice = 200 + sum;
  }
}
