































































import { Component, Vue, Watch } from "vue-property-decorator";
import LeftWing from "@/components/LeftWing.vue";
import RightWing from "@/components/RightWing.vue";
import StylesBox from "@/components/RightWingModule/StylesBox.vue";
import SizeBox from "@/components/RightWingModule/SizeBox.vue";
import BeadBox from "@/components/RightWingModule/BeadBox.vue";
import StringColor from "@/components/RightWingModule/StringColor.vue";
import LockStyle from "@/components/RightWingModule/LockStyle.vue";
import SizeBeadsBox from "@/components/RightWingModule/SizeBeadsBox.vue";
@Component({
  components: {
    LeftWing,
    RightWing,
    StylesBox,
    SizeBox,
    BeadBox,
    StringColor,
    LockStyle,
    SizeBeadsBox,
  },
})
export default class Home extends Vue {
  colorOfFrontString: string = "./assets/StrungSmallBlack.png";
  colorOfBackString: string = "./assets/Black_S.png";
  colorOfLock: string = "./assets/SmallGold.png";

  sizeBase: string = "strung_medium_women";
  customColorName: string = "Đen";
  customLockName: string = "Mạ vàng 18 kara";
  customStyleOfString: string = "Classic With String";
  sizeBead: string = "8mm";
  showAny: string = "";
  showStyle: boolean = false;
  showSize: boolean = false;
  showBead: boolean = false;
  showLock: boolean = false;
  showStyleOfString: boolean = false;
  showColorOfString: boolean = false;

  // begin Change size

  @Watch("sizeBase")
  wSizeBase() {
    if (this.sizeBase == "strung_large_women") {
      this.colorOfFrontString = "./assets/StrungMediumBlack.png";
      this.colorOfBackString = "./assets/Black_M.png";
      this.colorOfLock = "./assets/MediumGold.png";
    } else if (this.sizeBase == "strung_medium_women") {
      this.colorOfFrontString = "./assets/StrungSmallBlack.png";
      this.colorOfBackString = "./assets/Black_S.png";
      this.colorOfLock = "./assets/SmallGold.png";
    } else if (this.sizeBase == "strung_small_women") {
      this.colorOfFrontString = "./assets/StrungXSmallBlack.png";
      this.colorOfBackString = "./assets/Black_XS.png";
      this.colorOfLock = "./assets/XSmallGold.png";
    } else if (this.sizeBase == "strung_xlarge_women") {
      this.colorOfFrontString = "./assets/StrungLargeBlack.png";
      this.colorOfBackString = "./assets/Black_L.png";
      this.colorOfLock = "./assets/LargeGold.png";
    }
  }

  // End Change size

  checkShow(value: string) {
    this.showAny = value;
  }
  resetStyle() {
    this.showStyle = false;
    this.showAny = "";
  }
  resetSize() {
    this.showSize = false;
    this.showAny = "";
  }
  resetBead() {
    this.showBead = false;
    this.showAny = "";
  }
  resetLock() {
    this.showLock = false;
    this.showAny = "";
  }
  resetColorOfString() {
    this.showColorOfString = false;
    this.showAny = "";
  }
  resetStyleOfString() {
    this.showStyleOfString = false;
    this.showAny = "";
  }
  everythingFalse() {
    this.showStyle = false;
    this.showSize = false;
    this.showBead = false;
    this.showLock = false;
    this.showStyleOfString = false;
    this.showColorOfString = false;
  }
  changeColor(value: string) {
    this.colorOfFrontString = value[0];
    this.colorOfBackString = value[1];
    this.customColorName = value[2];
  }
  changeLock(value: string) {
    this.colorOfLock = value[0];
    this.customLockName = value[1];
  }
  changeSize(value: string) {
    this.sizeBase = value;
    this.sizeBead = "8mm";
  }
  @Watch("showAny")
  watchShowAny() {
    if (this.showAny == "style") {
      this.everythingFalse();
      this.showStyle = true;
    } else if (this.showAny == "size") {
      this.everythingFalse();
      this.showSize = true;
    } else if (this.showAny == "bringBeads") {
      this.everythingFalse();
      this.showBead = true;
    } else if (this.showAny == "colorBox") {
      this.everythingFalse();
      this.showColorOfString = true;
    } else if (this.showAny == "lockBox") {
      this.everythingFalse();
      this.showLock = true;
    } else if (this.showAny == "sizeBeadsBox") {
      this.everythingFalse();
      this.showStyleOfString = true;
    } else {
      this.everythingFalse();
    }
  }
  switchStyle(value: string) {
    this.customStyleOfString = value;
  }
  @Watch("customStyleOfString")
  wChangeStyleOfString() {
    if (this.customStyleOfString == "Classic With String") {
      this.sizeBase = "strung_medium_women";
      this.sizeBead = "8mm";
      this.colorOfFrontString = "./assets/StrungSmallBlack.png";
      this.colorOfBackString = "./assets/Black_S.png";
      this.colorOfLock = "./assets/SmallGold.png";
    }
    if (this.customStyleOfString == "Classic") {
      this.sizeBase = "classic_medium8_women";
      this.sizeBead = "8mm";
      this.colorOfFrontString = "./assets/ClassicMediumBlack.png";
      this.colorOfBackString = "";
      this.colorOfLock = "./assets/classic_gold.png";
    }
    if (this.customStyleOfString == "Elastic") {
      this.sizeBase = "elastic_medium8_women";
      this.sizeBead = "8mm";
      this.colorOfFrontString = "./assets/elastic.png";
      this.colorOfBackString = "";
      this.colorOfLock = "./assets/elastic_gold.png";
    }
  }

  changeSizeBead(val: any) {
    this.sizeBase = val[0];
    this.sizeBead = val[1];
  }
}
